[data-module='calendar'] [aria-live] {
  clip: rect(1px 1px 1px 1px);
  /* IE 6/7 */
  clip: rect(1px, 1px, 1px, 1px);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px; }

.calendar {
  background-color: #74a126;
  color: #fff;
  font: 14px/1 'Roboto', sans-serif;
  margin: 0; }
  .calendar a {
    text-decoration: none; }

.calender__caption {
  background-color: #558307;
  caption-side: top;
  line-height: 50px;
  margin: 0;
  padding: 0;
  text-align: center;
  text-transform: uppercase; }
  .calender__caption div {
    display: flex; }

.calender__caption__text {
  display: block;
  flex-grow: 1;
  text-align: center;
  text-transform: uppercase; }

.calendar__prev,
.calendar__next {
  appearance: none;
  background-color: transparent;
  border: none;
  color: #fff;
  display: block;
  position: relative;
  width: 30px; }
  .calendar__prev::before,
  .calendar__next::before {
    border-color: #fff;
    border-style: solid;
    border-width: 2px 2px 0 0;
    content: '';
    display: block;
    height: 8px;
    position: absolute;
    top: 50%;
    width: 8px; }

.calendar__prev::before {
  left: 10px;
  transform: translate3d(0, -50%, 0) rotate(225deg); }

.calendar__next::before {
  right: 10px;
  transform: translate3d(0, -50%, 0) rotate(45deg); }

.calendar__weekday {
  font-weight: 400;
  padding-top: 1em;
  text-align: center; }

.calendar__day {
  padding: 1px; }

.calendar__day__number {
  border: 2px solid transparent;
  border-radius: 50%;
  display: block;
  line-height: 20px;
  text-align: center;
  transition: all 0.2s ease;
  width: 24px; }
  .calendar__day__number.-upcoming {
    background-color: #fff;
    color: #74a126; }
    .calendar__day__number.-upcoming:focus, .calendar__day__number.-upcoming:hover {
      background-color: #74a126;
      border-color: #fff;
      color: #fff; }
  .calendar__day__number.-today {
    background-color: #000;
    color: #fff; }
    .calendar__day__number.-today.-upcoming {
      border-color: #fff; }
  .calendar__day__number.-lastmonth, .calendar__day__number.-nextmonth {
    color: rgba(255, 255, 255, 0.3); }

*,
*::before,
*::after {
  box-sizing: border-box; }
